@charset "UTF-8";
:root {
  --color-orange: #E87722;
  --color-yellow: #F5CD26;
  --color-black: #231F20;
  --color-text: #231F20;
  --color-white: #FFFFFF;
  --color-gray: #E5E5E5;
  --color-gray-light: #FAFAFA;
  --color-gray-light-1: #FAFAFA;
  --color-gray-light-2: #F7F7F7;
  --color-gray-light-3: #F2F2F2;
  --color-gray-light-4: #EAEAEA;
  --color-gray-darker: #999999;
  --color-gray-darker-to-white: #999999;
  --color-gray-darker-2: #848283;
  --color-blue: #0051CC;
  --color-blue-hover: #0065FF;
  --body-font-family: "Open Sans", sans-serif, ;
}

.vertical-tabs {
  padding: 20px;
}
.vertical-tabs .nav-pills {
  display: none;
}
.vertical-tabs .card-body {
  position: relative;
  display: flex;
  border: 1px solid var(--color-gray);
  border-radius: 10px;
}
.vertical-tabs .nav-link {
  font-weight: bold;
  font-family: "Source Sans Pro", sans-serif;
  color: var(--color-text);
  font-size: 26px;
  line-height: 28px;
}
.vertical-tabs iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.vertical-tabs .youtube-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
}
@media (min-width: 768px) {
  .vertical-tabs .nav-pills {
    display: flex;
    align-self: baseline;
  }
  .vertical-tabs .nav-pills .nav-link {
    position: relative;
    text-decoration: none;
    color: var(--color-gray-darker-to-white);
    font-weight: 700;
    width: 100%;
    padding: 10px;
    text-align: left;
    display: flex;
  }
  .vertical-tabs .nav-pills .nav-link:before {
    content: "◼";
    width: 10px;
    height: 10px;
    position: absolute;
    top: 11px;
    left: -20px;
  }
  .vertical-tabs .nav-pills .nav-link.active {
    color: var(--color-orange);
    background-color: transparent;
  }
  .vertical-tabs .tab-content .tab-pane h5 a {
    display: none;
  }
  .vertical-tabs .tab-pane .collapse {
    display: block;
  }
  .vertical-tabs .tab-pane .image-container {
    border: none;
  }
  .vertical-tabs .tab-pane .col-md-6 {
    display: flex;
    flex-direction: column;
  }
  .vertical-tabs .tab-pane .col-md-6:nth-child(2) {
    padding: 15px;
  }
  .vertical-tabs .tab-pane .col-md-6 .btn-iccwc.btn-next {
    margin-top: auto;
    margin-right: auto;
  }
  .vertical-tabs .tab-pane p {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 7;
    line-clamp: 7;
    -webkit-box-orient: vertical;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    color: var(--color-black);
  }
  .vertical-tabs .tab-pane figure {
    margin: 21.5px 14.5px;
  }
}
@media (max-width: 767px) {
  .vertical-tabs .tab-content > .tab-pane {
    display: block !important;
    opacity: 1;
  }
  .vertical-tabs .tab-pane h5 a {
    position: relative;
    text-decoration: none;
    color: var(--color-gray-darker-to-white);
    font-weight: 700;
    width: 100%;
    padding: 10px;
    text-align: left;
    display: flex;
    margin-left: 20px;
  }
  .vertical-tabs .tab-pane h5 a:before {
    content: "◼";
    width: 10px;
    height: 10px;
    position: absolute;
    top: 9px;
    left: -20px;
  }
  .vertical-tabs .tab-pane h5 a:hover {
    text-decoration: none;
  }
  .vertical-tabs .tab-pane h5 a[aria-expanded=true] {
    color: var(--color-orange);
  }
  .vertical-tabs .card-body {
    padding: 16px 5px;
  }
  .vertical-tabs p {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 8;
    line-clamp: 8;
    -webkit-box-orient: vertical;
  }
}